import React from "react";
// react library for routing
import { Route, Switch, Redirect } from "react-router-dom";
// core components
import AdminNavbar from "components/Navbars/AdminNavbar.js";
import AdminFooter from "components/Footers/AdminFooter.js";
import Sidebar from "components/Sidebar/Sidebar.js";
import * as dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";

import routesImport from "routes.js";

const $ = window;

$.dayjs = dayjs;
$.dayjs.extend(relativeTime);

$.io = require("socket.io-client/dist/socket.io");

function connect() {
  $.socket = $.io({
    query: `type=website`,
  });
}

connect();

$.socket.on("disconnect", () => {
  // setTimeout(() => connect(), 5000);
});
let routes;
class Admin extends React.Component {
  constructor(props) {
    super(props);
    this.notification = React.createRef(Admin);
  }

  state = {
    sidenavOpen: true,
    routes: [],
    me: {},
  };

  async componentDidMount() {
    if ($.socket) $.socket.open();

    $.socket.on("notification", (data) => {
      console.log(data);
      if (this.notification.current.notificationPush) {
        console.log(
          "WTF",
          this.notification.current,
          this.notification.current.notificationPush
        );
        this.notification.current.notificationPush(data);
      }
    });
  }

  componentDidUpdate(e) {
    if (e.history.pathname !== e.location.pathname) {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      this.refs.mainContent.scrollTop = 0;
    }
  }

  async componentWillMount() {
    const me = await fetch("/me", {
      method: "get",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .catch((e) => {
        console.log(e);
      })
      .then((me) => me.json());

    routes = await routesImport(me);
    this.setState({ me });
    this.setState({ routes });
  }

  getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.collapse) {
        return this.getRoutes(prop.views);
      }
      if (prop.layout === "/admin") {
        const Component = prop.component;
        return (
          <Route
            path={prop.layout + prop.path}
            render={(props) => {
              return (
                <Component
                  {...props}
                  me={this.state.me}
                  ref={this.notification}
                />
              );
            }}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };
  getBrandText = (path) => {
    // console.log(path, routes.length, routes[1]);
    // for (let i = 0; i < routes.length; i++) {
    //   if (
    //     this.props.location.pathname.indexOf(
    //       routes[i].layout + routes[i].path
    //     ) !== -1
    //   ) {
    //     return routes[i].name;
    //   }
    // }
    // return "Brand";
  };
  // toggles collapse between mini sidenav and normal
  toggleSidenav = (e) => {
    if (document.body.classList.contains("g-sidenav-pinned")) {
      document.body.classList.remove("g-sidenav-pinned");
      document.body.classList.add("g-sidenav-hidden");
    } else {
      document.body.classList.add("g-sidenav-pinned");
      document.body.classList.remove("g-sidenav-hidden");
    }
    this.setState({
      sidenavOpen: !this.state.sidenavOpen,
    });
  };
  getNavbarTheme = () => {
    return this.props.location.pathname.indexOf(
      "admin/alternative-dashboard"
    ) === -1
      ? "dark"
      : "light";
  };
  render() {
    return (
      <>
        <Sidebar
          {...this.props}
          routes={this.state.routes}
          toggleSidenav={this.toggleSidenav}
          sidenavOpen={this.state.sidenavOpen}
          logo={{
            innerLink: "/",
            imgSrc: require("assets/img/brand/argon-react.png"),
            imgAlt: "...",
          }}
        />
        <div
          className="main-content"
          ref="mainContent"
          onClick={this.closeSidenav}
        >
          <AdminNavbar
            {...this.props}
            theme={this.getNavbarTheme()}
            toggleSidenav={this.toggleSidenav}
            sidenavOpen={this.state.sidenavOpen}
            brandText={this.getBrandText(this.props.location.pathname)}
          />
          <Switch>
            {this.getRoutes(this.state.routes)}
            <Redirect from="*" to="/admin/dashboard" />
          </Switch>
          <AdminFooter />
        </div>
        {this.state.sidenavOpen ? (
          <div className="backdrop d-xl-none" onClick={this.toggleSidenav} />
        ) : null}
      </>
    );
  }
}

export default Admin;
